<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
        <el-button type="primary"  icon="el-icon-plus" @click="dialogFormVisible=true" ></el-button>
        <!--<el-button type="danger"  icon="el-icon-close"></el-button>-->
    </div>
    <div class="searchbar">
        <el-row>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  prefix-icon="el-icon-search" v-model="criteria.userName" ></el-input> </el-col>
             <el-col :span="4">
                 <el-select v-model="criteria.userType" placeholder="">
                    <el-option v-for="item in criteria.userTypes" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                 </el-select>
            </el-col>            
            <el-col :span="4">
                <el-date-picker  v-model="criteria.startDate" type="date"  placeholder=""> </el-date-picker>
            </el-col>
            <el-col :span="4">
                <el-date-picker  v-model="criteria.endDate" type="date"  placeholder=""> </el-date-picker>
            </el-col>


            <el-col :span="4"><el-button type="primary" icon="el-icon-search" @click="search"></el-button></el-col>
        </el-row>
        
        

    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="userName"   label="" ></el-table-column>
            <el-table-column  prop="userType"   label="" >
                <template scope="scope">
                    <span v-if="scope.row.userType==0" style="color:green;" ></span>
                    <span v-if="scope.row.userType==1" style="color:red;" ></span>
                    <span v-if="scope.row.userType==2" style="color:green;" ></span>
                    <span v-if="scope.row.userType==3" style="color:red;" ></span>
                </template>
            </el-table-column>
             <el-table-column  prop="creditScore"   label="" ></el-table-column>
             <el-table-column  prop="needAmount"   label="" ></el-table-column>
             <el-table-column  prop="needCycle"   label="" ></el-table-column>
             <el-table-column  prop="mobile"   label="" ></el-table-column>
             <el-table-column  prop="realName"   label="" ></el-table-column>
            <el-table-column  prop="createTime"   label="" :formatter="dateFormat" ></el-table-column>
            <el-table-column  prop="rowStatus"   label="" >
                <template scope="scope">
                    <span v-if="scope.row.useStatus==0" style="color:#E6A23C;" ></span>
                    <span v-if="scope.row.useStatus==1" style="color:green;" ></span>
                </template>
            </el-table-column>
            <el-table-column label=""  > 
                 <template slot-scope="scope">
                    <el-button size="small" type="primary"  @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button size="small" type="danger"   @click="handleDelete(scope.$index, scope.row)"></el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
<!-- form -->
<el-dialog title="" :visible.sync="dialogFormVisible" :before-close="close" :width="dialogWidth">
    <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="" :label-width="formLabelWidth">
        <input type="hidden"   v-model="form.id" />
            <el-select v-model="form.userType" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>

            </el-select>
        </el-form-item>
         <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.useStatus" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" prop="userName">
            <input type="hidden" v-model="form.id"/>
            <input type="hidden" v-model="form.userCode"/>
            <el-input v-model="form.userName" auto-complete="off" size="small"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="userPassword">
            <el-input v-model="form.userPassword" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="realName">
              <el-input v-model="form.realName" auto-complete="off" size="small"></el-input>
         </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
            <el-input v-model="form.mobile" auto-complete="off" size="small"></el-input>
        </el-form-item>
         <el-form-item label="" :label-width="formLabelWidth" prop="email">
              <el-input v-model="form.email" auto-complete="off" size="small"></el-input>
         </el-form-item>
         <el-form-item label="" :label-width="formLabelWidth">
              <el-col :span="10">
                <el-select  class="w8"  v-model="form.sex" placeholder="">
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                </el-select>
              </el-col>
              <el-col :span="2">/</el-col>
             <el-col :span="6">
                <el-input type="number" class="w8" v-model="form.age" auto-complete="off"></el-input>
             </el-col>
             <el-col :span="2"></el-col>
            
         </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="creditScore">
             <el-col :span="10">
               <el-input type="number" v-model="form.creditScore" auto-complete="off"></el-input>
             </el-col>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth" prop="needAmount">
            <el-col :span="10">
             <el-input type="number" v-model="form.needAmount" auto-complete="off"></el-input>
             </el-col>
          </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="needCycle">
              <el-col :span="10">
                <el-input type="number" v-model="form.needCycle" auto-complete="off"></el-input>
              </el-col>
          </el-form-item>
        
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false"> </el-button>
        <el-button type="primary" @click="formSave('form')"> </el-button>
    </div>

</el-dialog>
</div>
</template>

<script>
import {getCompanyUserList,addCompanyUser,updateCompanyUser,delUser} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companyuser',
  data () {
      return{
             tableData:[],
             //-
             pagesize: 10,
             //-
             currentPage: 1,
             //-
             totalCount: 100,
             //
             criteria: {
                 userName:'',
                 startDate:'',
                 endDate:'',
                 userType:'',
                 userTypes:[
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''},
                     {value: '2', label: ''},
                     {value: '3', label: ''},
                 ],
                 userCompany:'',
                 userCompanys:[
                     {value: '', label: ''},
                 ]
             },
             multipleSelection: [],
             form:{                
                 id:'',
                 userType:'0',
                 useStatus:'0',
                 companyName:'',
                 companyId:'',  
                 userName:'',
                 userPassword:'',
                 realName:'',
                 mobile:'',
                 email:'',
                 sex:"0",
                 age:'18',
                 creditScore:'0',
                 needAmount:'0',
                 needCycle:'0'
             },
             rules:{
                userName:[
                     { required: true, message: '', trigger: 'blur' },
                     { min: 6, max:20, message: ' 6  20 ', trigger: 'blur' }
                    ],
                password:[ 
                     {   max:20, message:' 20  ', trigger: 'blur' }
                    ],
                email:[ 
                     {    max:30, message:' 40  ', trigger: 'blur' }
                    ],
                mobile:[ 
                     {    max:18, message:' 18  ', trigger: 'blur' }
                    ],
                realName:[ 
                     {    max:18, message:' 16  ', trigger: 'blur' }
                    ]
                    
             },
             dialogFormVisible: false,
             dialogWidth:'400px',
             formLabelWidth: '80px'
             
      }
  },
  mounted(){
     var self=this;
     self.reload();
    // self.getRoleList();
  },
  methods:{
     //
     loadData: function(criteria, pageNum, pagesize){ 
          console.log("load");
        var self=this;
        var params={page:pageNum,size:pagesize,search:""};
        if(criteria!=null){
            params.search=JSON.stringify({
                 userName:criteria.userName,
                 startDate:criteria.startDate,
                 endDate:criteria.endDate,
                 userType:criteria.userType,
                 userCompany:criteria.userCompany
            });    
        }
        getCompanyUserList(params).then(result => {
                var data=result.data;
                self.pagesize=data.pageSize;
                self.totalCount=data.total;
                self.tableData=data.list;
        });
     },
     reload:function(){
         var self=this;
          self.loadData(null,self.currentPage,self.pagesize);
     },
     restForm:function(){
        var self=this;
        self.form={id:'',  userType:'0',useStatus:'0',companyName:'',companyId:'',  userName:'',userPassword:'',realName:'', mobile:'',email:'',sex:"0",age:'18',creditScore:'0', needAmount:'0',needCycle:'0'};
        self.dialogFormVisible=false;
     },
     close:function(){
         var self=this;
         self.restForm();
     },
     //
     handleSizeChange: function(val) {
        this.pagesize = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //
     handleCurrentChange: function(val) {
        this.currentPage = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //
     handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      dateFormat:function(row, column) {  
           var self=this;
          var date = row[column.property];  
          if (date == undefined) {  
             return "";  
          }  
          return MomentPlugin(date).format("YYYY-MM-DD HH:mm:ss"); 
     },
     //
      getRoleList:function(){
         var self=this;
        getRoleDrop({}).then(result => {
            console.log("drop",result);
            if(result.code!="200")return;
            self.criteria.userCompanys=result.data;
            self.criteria.userCompanys.splice(0,0,{text:'',value:''});
        });
      },
      //
      search:function(){
          var self=this;
          console.log("search");
          this.loadData(self.criteria, this.currentPage, this.pagesize);
      },
    //
     handleEdit: function(index, row) {
        console.log("e:",index, row);
        var self=this;
        var detail=self.form;
        detail.id=row.id;
        detail.userType=row.userType.toString();
        detail.useStatus=row.useStatus.toString();    
        detail.companyId=row.companyId;
        detail.userName=row.userName;
        detail.userPassword='';
        detail.realName=row.realName;
        detail.email=row.email;
        detail.mobile=row.mobile;
        detail.sex=row.sex.toString();
        detail.age=row.age;
        detail.creditScore=row.creditScore;
        detail.needAmount=row.needAmount;
        detail.needCycle=row.needCycle;
        self.dialogFormVisible=true;
     },
     //
     handleDelete: function(index, row) {
          console.log("d:",index, row);
          var self=this;
          if(!confirm(""))return;
          delUser({id:row.id}).then(result => {  
              console.log('del:',result);
             if(result.code!="200"){
                alert(result.message);
                return;
             }        
             self.reload();  
          });
     },
     //
     formSave(formName){
        var self=this;
        self.$refs[formName].validate((valid) => {
            console.log("valid:",valid);
            if(!valid)return;
          
            if(self.form.id==""){
                //add
                addCompanyUser(self.form).then(result => {
                    console.log("add:",result);
                    if(result.code!="200"){
                        alert(result.message);
                        return;
                    }                  
                    self.reload();
                    self.restForm();
                   
                });
            }else{
                //update
                updateCompanyUser(self.form).then(result => {      
                     if(result.code!="200"){
                        alert(result.message);
                        return;
                     }              
                     self.reload();
                     self.restForm();
                     
                });
            }
       });
       

      }
  }
}
</script>


