<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
        <el-button type="primary"  icon="el-icon-plus" @click="dialogFormVisible=true" ></el-button>
    </div>
    <div class="searchbar">
        <el-row>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  prefix-icon="el-icon-search" v-model="criteria.roleName" ></el-input> </el-col>          
            <el-col :span="4">  
                <el-select v-model="criteria.userStatus" placeholder="">
                    <el-option v-for="item in criteria.status" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                 </el-select>
            </el-col>
            <el-col :span="4"><el-button type="primary" icon="el-icon-search" @click="search"></el-button></el-col>
            <el-col :span="12"></el-col>
        </el-row>               
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="" ></el-table-column>
            <el-table-column  prop="parentId"   label="" ></el-table-column>
            <el-table-column  prop="roleName"   label="" ></el-table-column>
            <el-table-column  prop="userStatus"   label="">
                <template scope="scope">
                    <span v-if="scope.row.userStatus==0" style="color:green;" ></span>
                    <span v-if="scope.row.userStatus==1" style="color:red;" ></span>                
                </template>
            </el-table-column>
            <el-table-column  prop="description"   label="" ></el-table-column>
            <el-table-column label=""  > 
                 <template slot-scope="scope">
                    <el-button size="small" type="primary"  @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button size="small" type="danger"   @click="handleDelete(scope.$index, scope.row)"></el-button>
                    <el-button size="small" type="success"   @click="rolePermision(scope.$index, scope.row)"></el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
<!-- form -->
<el-dialog title="" :visible.sync="dialogFormVisible" :before-close="close" :width="dialogWidth">
    <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="" :label-width="formLabelWidth" prop="roleName">
             <input type="hidden" v-model="form.id"/>
            <el-input v-model="form.roleName" auto-complete="off" size="small"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="description">
            <el-input v-model="form.description" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="parentId">
            <el-input v-model="form.parentId" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.userStatus" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false"> </el-button>
        <el-button type="primary" @click="formSave('form')"> </el-button>
    </div>

</el-dialog>
<!---->
<el-dialog title="" :visible.sync="dialogPermisionVisible" :before-close="roleClose" :width="dialogRoleWidth"  >
<el-row>
  <el-col :span="24" style="text-align:right;margin-bottom: 16px">
     <el-button type="success"   @click="savePermissions" ></el-button>
  </el-col>
</el-row>
<el-row :gutter="20">

     <el-tree
        :data="treeData"
        show-checkbox
        default-expand-all
        node-key="id"
        ref="tree"
        highlight-current
        :props="defaultProps"        
        >
    </el-tree>   
</el-row>

</el-dialog>
</div>
</template>

<script>
import {addRole,delRole,updateRole,getRoleList,getPermission,getRolePermission,savePermission} from '@/api'

export default {
  name: 'rolemanage',
  data () {
      return{
             tableData:[],
             tableRoleData:[],
             treeData:[],
            defaultProps: {
                children: 'children',
                label: 'label'
             },
             roleCode:"",
             //-
             pagesize: 10,
             //-
             currentPage: 1,
             //-
             totalCount: 100,
             //
             criteria: {
                 appName:'',
                 userStatus:'',
                 status:[
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''}
                 ]
             },
            criteriaRole: {
                 appName:'',
                 appCode:'',
                 routeType:'',
                 routeTypes:[
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''}
                 ],
                 routeStatus:'',
                 status:[
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''}
                 ],
                 routeMethod:'',
                 routeMethods:[
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''},
                     {value: '2', label: 'API'},
                     {value: '3', label: ''}
                 ],
                 appList:[]
             },
             multipleSelection: [],
             multipleRoleSelection: [],
             hasPermision:[],
             form:{
                 id:'',                
                 roleName:'',
                 userStatus:'0',
                 roleFlag:''      
             },
             rules:{
                roleName:[
                     { required: true, message: '', trigger: 'blur' },
                     { min: 2, max:25, message: ' 2  25 ', trigger: 'blur' }
                    ],
                roleFlag:[ 
                    { required: true, message: '', trigger: 'blur' },
                     {  max:20, message: '20 ', trigger: 'blur' }
                    ]
             },
             dialogFormVisible: false,
             dialogWidth:'400px',
             formLabelWidth: '80px',
             dialogPermisionVisible:false,
             dialogRoleWidth:'600px',
             dialogRoleHeight:'600px'
             
      }
  },
  mounted(){
     var self=this;
     self.reload();

  },
  methods:{
     //
     loadData: function(criteria, pageNum, pagesize){ 
        var self=this;
        var params={page:pageNum,size:pagesize,search:""};
        if(criteria!=null){
            params.search=JSON.stringify({roleName:criteria.roleName,userStatus:criteria.userStatus});    
        }
        getRoleList(params).then(result => {
                var data=result.data;
                self.pagesize=data.pageSize;
                self.totalCount=data.total;
                self.tableData=data.list;
        });
     },
     reload:function(){
         var self=this;
          self.loadData(null,self.currentPage,self.pagesize);
     },
     restForm:function(){
        var self=this;
        self.form={id:'',roleName:'',userStatus:'0',roleFlag:''};
        self.dialogFormVisible=false;
     },
     close:function(){
         var self=this;
         self.restForm();
     },
     roleClose:function(){
          var self=this;
          self.roleCode="";
          self.treeData=[];
          self.dialogPermisionVisible=false;
     },
     //
     handleSizeChange: function(val) {
        this.pagesize = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     //
     handleCurrentChange: function(val) {
        this.currentPage = val;
        this.loadData(this.criteria, this.currentPage, this.pagesize);
     },
     handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //
      search:function(){
          var self=this;
          this.loadData(self.criteria, this.currentPage, this.pagesize);
      },
      //
      loadPermision:function(){
          var self=this;              
         getPermission({roleCode:self.roleCode}).then(result => {
              self.treeData=self.menuToItems(result.data);     
              self.showPermision();      
          });
      },
      isNull(el){
        return typeof(el) == "undefined" ||el==null || el=="";
      },
       /**
       * 
       */
      menuToItems(menu){
         const self = this;
         if(self.isNull(menu)){
           return [];
         }
         var eachFn=function(pid,data){
           var jitems=[];
           for(var i=0;i<data.length;i++){
             var npid=menu[i]["parentId"];
             if(npid==pid){
                var jitem={};
                jitem.label=menu[i].rightName;
                jitem.id=menu[i].id;
                jitem.children=eachFn(menu[i]["id"],menu);
                jitems.push(jitem);
             }
           }
           return jitems;
         };
         var items=[];
         for(var i=0;i<menu.length;i++){
             var pid=menu[i]["parentId"];
             if(self.isNull(pid)){
               var item={};
               item.label=menu[i].rightName;
               item.id=menu[i].id;
               item.children=eachFn(menu[i]["id"],menu);
               items.push(item);
             }
          }
       return items;
      },
      //
      showPermision:function(){
        var self=this;          
        getRolePermission({roleId:self.roleCode}).then(result => {
            if(result.code!=200){alert(result.message); return;}
            console.log(result);
            self.$refs.tree.setCheckedKeys(result.data);
         });
      },
      //
      rolePermision:function(index,row){
         var self=this;
         self.roleCode=row.id;
         self.dialogPermisionVisible=true;
         self.loadPermision();
      
      },
      //
      savePermissions:function(){
        var  self=this;
        var params={roleId:self.roleCode,ids:""};      
        var ids="";
        self.$refs.tree.getCheckedNodes().forEach(p=>{ ids+=p.id+"," });
        if(ids.length>0)ids=ids.substring(0,ids.length-1);
        params.ids=ids;
        savePermission(params).then(result => {
            if(result.code==200){
                alert("!");
            }
        });
      },
    //
     handleEdit: function(index, row) {
        var self=this;
        var detail=self.form;
        detail.id=row.id;
        detail.parentId=row.parentId;
        detail.roleName=row.roleName;
        detail.userStatus=row.userStatus.toString();
        detail.description=row.description;
        self.dialogFormVisible=true;
     },
     //
     handleDelete: function(index, row) {
          var self=this;
          if(!confirm(""))return;
          delRole({id:row.id}).then(result => {  
             if(result.code!="200"){
                alert(result.message);
                return;
             }        
             self.reload();  
          });
     },
     //
     formSave(formName){
        var self=this;
        self.$refs[formName].validate((valid) => {
            if(!valid)return;
          
            if(self.form.id==""){
                //add
                addRole(self.form).then(result => {
                    if(result.code!="200"){
                        alert(result.message);
                        return;
                    }                  
                    self.reload();
                    self.restForm();
                   
                });
            }else{
                //update
                updateRole(self.form).then(result => {      
                     if(result.code!="200"){
                        alert(result.message);
                        return;
                     }              
                     self.reload();
                     self.restForm();
                     
                });
            }
       });
       

      }
  }
}
</script>


