<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row :gutter="20">
            <el-col :span="4"><el-input  placeholder="ID"   class="w18"  v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.assetType" placeholder="">
                <el-option v-for="item in criteria.aType" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.everyDeadline" placeholder="">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search"></el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search"></el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search"></el-button></el-col>
        </el-row>
        <el-row>
            <el-col :span="4"><el-input  placeholder=""   class="w18" v-model="criteria.everySum" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  v-model="criteria.fullName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18" v-model="criteria.abbreviationName" ></el-input> </el-col>

        </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="ID" ></el-table-column>
            <el-table-column  prop="abbreviation_name"   label="" ></el-table-column>
            <el-table-column  prop="asset_size"   label="" ></el-table-column>
            <el-table-column  prop="month_size"   label="" ></el-table-column>
            <el-table-column  prop="every_deadline"   label="" ></el-table-column>
            <el-table-column  prop="every_sum"   label="" ></el-table-column>
            <el-table-column  prop="overdue"   label="" ></el-table-column>
            <el-table-column  prop="accept_found"   label="" ></el-table-column>
            <el-table-column  prop="business_area"   label="" ></el-table-column>
            <el-table-column  prop="is_performance_insurance"  label="" >
              <template scope="scope">
                <span v-if="scope.row.is_performance_insurance==0"> </span>
                <span v-if="scope.row.is_performance_insurance==1" ></span>
              </template>
            </el-table-column>
            <el-table-column  prop="is_guarantee"  label="" >
              <template scope="scope">
                <span v-if="scope.row.is_guarantee==0"> </span>
                <span v-if="scope.row.is_guarantee==1"></span>
              </template>
            </el-table-column>
            <el-table-column label=""  >
                 <template slot-scope="scope">
                   <el-button size="small" type="danger"   @click="handleDeleteCooperation(scope.$index, scope.row)"></el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>
</div>
</template>

<script>
import {getMyAssetCompanyList,deleteCooperation} from '@/api'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'companylist',
  data () {
      return{
             tableData:[],
             //-
             pagesize: 10,
             //-
             currentPage: 1,
             //-
             totalCount: 100,
             //
             criteria: {
                 id:'',
                 assetType:'',
                 aType:[
                   {value: '', label: ''},
                   {value: '10', label: ''},
                   {value: '11', label: ''},
                   {value: '12', label: ''},
                   {value: '13', label: ''},
                   {value: '14', label: ''},
                   {value: '15', label: ''},
                   {value: '16', label: ''}
                 ],
                 everyDeadline:'',
                 eDeadline:[
                   {value: '', label: ''},
                   {value: '1', label: '0~3'},
                   {value: '2', label: '0~12'},
                   {value: '3', label: '0~24'},
                   {value: '4', label: '24'},
                 ],
                 everySum:'',
                 fullName:'',
                 abbreviationName:'',
                 businessArea:''
             },
             multipleSelection: [],
             dialogFormVisible: false,
             dialogWidth:'500px',
             formLabelWidth: '80px'

      }
  },
  mounted(){
     var self=this;
     self.reload();
  },
  methods: {
    //
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          asset_type:criteria.assetType,
          every_deadline:criteria.everyDeadline,
          every_sum:criteria.everySum,
          business_area:criteria.businessArea,
          full_name:criteria.fullName,
          abbreviation_name:criteria.abbreviationName
        });

      }
      getMyAssetCompanyList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(this.criteria, self.currentPage, self.pagesize);
    },
    restForm: function () {
      var self = this;
      self.form = {id: '', name: '', img: '', useStatus: '0'};
    },
    close: function () {
      var self = this;
      self.restForm();
    },
    //
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    //
    handleDeleteCooperation: function(index, row) {
      console.log(row);
      var self=this;
      if(!confirm(""))return;
      deleteCooperation({targetCompanyId:row.id}).then(result => {
        alert(result.message);
        if(result.code!="200"){
          return;
        }
        self.reload();
      });
    },
  }
}
</script>



