<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="searchbar">
        <el-row :gutter="20" >
            <el-col :span="4"><el-input  placeholder=""   class="w18"  prefix-icon="el-icon-search" v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.companyType" placeholder="">
                <el-option v-for="item in criteria.cType" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.auditStatus" placeholder="">
                <el-option v-for="item in criteria.aStatus" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>


            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search"></el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="search"></el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="handleExport"></el-button></el-col>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span ></span>
              <input type="file"  name="file" @change="getFile($event,'1')" />
            </span>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span ></span>
              <input type="file"  name="file" @change="getFile($event,'2')" />
            </span>
            <span class="fileinput-button el-button el-button--primary el-button--small">
              <span ></span>
              <input type="file"  name="file" @change="getFile($event,'3')" />
            </span>

        </el-row>
        <el-row>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  prefix-icon="el-icon-search" v-model="criteria.fullName" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  prefix-icon="el-icon-search" v-model="criteria.abbreviationName" ></el-input> </el-col>
             <el-col :span="4">
              <el-date-picker  v-model="criteria.startDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd HH:mm:ss"  placeholder=""> </el-date-picker>
            </el-col>
            <el-col :span="4">
              <el-date-picker  v-model="criteria.endDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd HH:mm:ss"  placeholder=""> </el-date-picker>
            </el-col>
        </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="ID" ></el-table-column>
            <el-table-column  prop="abbreviationName"   label="" ></el-table-column>
            <el-table-column  prop="fullName"   label="" ></el-table-column>
            <el-table-column  prop="companyType"  label="" >
              <template scope="scope">
                <span v-if="scope.row.companyType==0"></span>
                <span v-if="scope.row.companyType==1"></span>
              </template>
            </el-table-column>
            <el-table-column  prop="auditStatus"  label="" >
              <template scope="scope">
                <span v-if="scope.row.auditStatus==0"></span>
                <span v-if="scope.row.auditStatus==1"></span>
                <span v-if="scope.row.auditStatus==2"></span>
                <span v-if="scope.row.auditStatus==3"></span>
              </template>
            </el-table-column>
            <el-table-column  prop="createTime"   label="" ></el-table-column>
            <el-table-column label=""  >
                 <template slot-scope="scope">
                   <el-button size="small" type="primary"   @click="handleDetail(scope.$index, scope.row)"></el-button>
                   <el-button size="small" type="danger" v-if="scope.row.auditStatus==1"   @click="handleAudit(scope.$index, scope.row)"></el-button>
                 </template>
            </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>


