<template>
<div class="conwarp">
<!-- grid -->
<div class="grid">
    <div class="toolbar">
          <el-button type="primary"  v-show="criteria.assetCompanyMenu" icon="el-icon-plus" @click="addPersonItem()" ></el-button>
          <!-- <el-button type="primary"  icon="el-icon-plus" @click="dialogAssetitemFormVisible=true" ></el-button> -->
    </div>
    <div class="searchbar">
        <el-row :gutter="20">
            <el-col :span="4"><el-input  placeholder=""   class="w18"  v-model="criteria.id" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  v-model="criteria.borrower_name" ></el-input> </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18"  v-model="criteria.borrow_amount" ></el-input> </el-col>
            <el-col :span="2"><el-button type="primary" icon="el-icon-search" @click="search"></el-button></el-col>
            <el-col :span="2"><el-button type="primary"  v-show="criteria.assetCompanyMenu" @click="search"></el-button></el-col>
           <!--
            <el-col :span="2"><el-button type="primary" v-show="criteria.foundCompanyMenu" @click="###"></el-button></el-col>
           -->
            <el-col :span="2"><el-button type="primary" @click="handleExport"></el-button></el-col>
            <el-col :span="2"><el-button type="primary" @click="handleExportCar"></el-button></el-col>
        </el-row>
        <el-row>
            <el-col :span="4">
              <el-select v-model="criteria.borrow_deadline" placeholder="">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.repay_way" placeholder="">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4"><el-input  placeholder=""   class="w18" v-model="criteria.borrow_rate" ></el-input> </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.asset_info" placeholder="">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="criteria.use_status" placeholder="">
                <el-option v-for="item in criteria.eDeadline" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-row>
    </div>
    <div class="gmain">
            <el-table :data="tableData"  ref="multipleTable"  tooltip-effect="dark"  @selection-change="handleSelectionChange" stripe style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column  prop="id"   label="" ></el-table-column>
            <el-table-column  prop="borrower_name"   label="" ></el-table-column>
            <el-table-column  prop="borrow_amount"   label="" ></el-table-column>
            <el-table-column  prop="borrow_deadline"  label="" >
              <template scope="scope">
                <span v-if="scope.row.borrow_deadline==1"> 0~3</span>
                <span v-if="scope.row.borrow_deadline==2"> 0~12</span>
                <span v-if="scope.row.borrow_deadline==3"> 0~24</span>
                <span v-if="scope.row.borrow_deadline==4"> 24</span>
              </template>
            </el-table-column>
            <el-table-column  prop="repay_way"  label="" >
              <template scope="scope">
                <span v-if="scope.row.repay_way==1"></span>
                <span v-if="scope.row.repay_way==2"></span>
                <span v-if="scope.row.repay_way==3"></span>
              </template>
            </el-table-column>
          <el-table-column  prop="borrow_rate"   label="" ></el-table-column>
          <el-table-column  prop="asset_info"  label="" >
            <template scope="scope">
              <span v-if="scope.row.asset_info==10"></span>
              <span v-if="scope.row.asset_info==11"></span>
              <span v-if="scope.row.asset_info==12"></span>
              <span v-if="scope.row.asset_info==13"></span>
              <span v-if="scope.row.asset_info==14"></span>
              <span v-if="scope.row.asset_info==15"></span>
              <span v-if="scope.row.asset_info==16"></span>
            </template>
          </el-table-column>
          <el-table-column  prop="use_status"  label="" >
            <template scope="scope">
              <span v-if="scope.row.use_status==0"></span>
              <span v-if="scope.row.use_status==1"></span>
              <span v-if="scope.row.use_status==2"></span>
              <span v-if="scope.row.use_status==3"></span>
              <span v-if="scope.row.use_status==4"></span>
              <span v-if="scope.row.use_status==5"></span>
            </template>
          </el-table-column>
          <el-table-column label=""  >
               <template slot-scope="scope">
                 <!-- <el-button size="small" type="primary" @click="handleDetail(scope.$index, scope.row)"></el-button> -->
                 <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
                 <el-button size="small" type="danger"   v-if="scope.row.use_status==1" v-show="criteria.assetCompanyMenu"  @click="handleAudit(scope.$index, scope.row)"></el-button>
                 <el-button size="small" type="danger"  v-show="criteria.foundCompanyMenu" @click="handleAddCar(scope.$index, scope.row)"></el-button>
               </template>
          </el-table-column>
        </el-table>
        <div align="center" class="gpage">
                <el-pagination
                     background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20,30]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
                </el-pagination>
        </div>
    </div>
</div>

    <el-dialog title="" :visible.sync="dialogAssetitemFormVisible" :before-close="close1" :width="dialogWidth" class="demo-ruleForm">
      <el-form :model="personInfo" :rules="rules" ref="personInfo" label-width="100px">
      <el-tabs type="border-card" v-model="firstCardItemName">
        <el-tab-pane name="baseInfo" label="">
            <el-form-item label="" prop="name">
                <el-input v-model="personInfo.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="idNumber">
                <el-input v-model="personInfo.idNumber"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mobile">
                <el-input v-model="personInfo.mobile"></el-input>
            </el-form-item>
            <el-form-item label="" prop="sex">
                <el-select v-model="personInfo.sex">
                    <el-option v-for="item in sexList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="address">
                <el-input v-model="personInfo.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="personInfo.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="professionType">
                <el-select v-model="personInfo.professionType">
                    <el-option v-for="item in professionTypeList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="salary">
                <el-input v-model="personInfo.salary"></el-input>
            </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
              <el-form-item label="" prop="itemTitle">
                  <el-input v-model="personInfo.itemTitle"></el-input>
              </el-form-item>
              <el-form-item label="" prop="assetInfo">
                  <el-select v-model="personInfo.assetInfo">
                    <el-option v-for="item in assetInfoList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="" prop="borrowAmount">
                  <el-input v-model="personInfo.borrowAmount"></el-input>
              </el-form-item>
              <el-form-item label="" prop="borrowDeadline">
                  <el-select v-model="personInfo.borrowDeadline">
                    <el-option v-for="item in borrowDeadlineList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="" prop="repayWay">
                  <el-select v-model="personInfo.repayWay">
                    <el-option v-for="item in repayWayList" :key="item.value"  :label="item.label" :value="item.value"> </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="" prop="borrowRate">
                  <el-input v-model="personInfo.borrowRate"></el-input>
              </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
              <el-form-item label="" prop="idCardFirstUrl">
                  <img :src="personInfo.idCardFirstUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,1)" />
              </el-form-item>
              <el-form-item label="" prop="idCardSecondUrl">
                  <img :src="personInfo.idCardSecondUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,2)" />
              </el-form-item>
              <el-form-item label="" prop="bankCardFirstUrl">
                  <img :src="personInfo.bankCardFirstUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,3)" />
              </el-form-item>
              <el-form-item label="" prop="bankCardSecondUrl">
                  <img :src="personInfo.bankCardSecondUrl" width="20%" alt="front" />
                  <input type="file"  name="file" @change="getFile($event,4)" />
              </el-form-item>
        </el-tab-pane>
      </el-tabs>
      </el-form>
       <div slot="footer" class="dialog-footer">
          <el-button @click="close1()"> </el-button>
          <el-button type="primary" v-show="criteria.assetCompanyMenu" @click="toSavePerson()"> </el-button>
      </div>
    </el-dialog>

  <el-dialog title="" :visible.sync="dialogAuditFormVisible" :before-close="close" :width="dialogAuditWidth" class="demo-ruleForm">
    <el-form :model="audit" :rules="rules" ref="audit" label-width="80px">
      <el-form-item label="" prop="status">
        <el-select v-model="audit.status" placeholder="">
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close()"> </el-button>
      <el-button type="primary" @click="toAudit()"> </el-button>
    </div>
  </el-dialog>

</div>
</template>

<script>
import {getAssetItemPoolList,auditAssetItem,uploadFile,addPersonItem,itemDetail,addCar,exportAssetItem,exportAssetItemCar} from '@/api'
import {idcardRule,mobileRule,emailRule,decimal_10_2,number_11_null} from '@/libs/Regular'
import MomentPlugin from '@/libs/MomentPlugin'
export default {
  name: 'assetitem',
  data () {
      return{
             tableData:[],
             //-
             pagesize: 10,
             //-
             currentPage: 1,
             //-
             totalCount: 100,
             firstCardItemName:'baseInfo',
             //
             criteria: {
                 id:'',
                 borrower_name:'',
                 borrow_amount:'',
                 borrow_deadline:'',
                 repay_way:'',
                 borrow_rate:'',
                 asset_info:'',
                 use_status:'',
                 bDeadline:[
                   {value: '', label: ''},
                   {value: '1', label: '0~3'},
                   {value: '2', label: '0~12'},
                   {value: '3', label: '0~24'},
                   {value: '4', label: '24'}
                 ],
                 rWay:[
                   {value: '', label: ''},
                   {value: '1', label: ''},
                   {value: '2', label: ''},
                   {value: '3', label: ''}
                 ],

                 aInfo:[
                   {value: '11', label: ''},
                   {value: '12', label: ''},
                   {value: '13', label: ''},
                   {value: '14', label: ''},
                   {value: '15', label: ''},
                   {value: '16', label: ''},
                   {value: '10', label: ''},
                 ],
                 uStatus:[
                   {value: '', label: ''},
                   {value: '0', label: ''},
                   {value: '1', label: ''},
                   {value: '2', label: ''},
                   {value: '3', label: ''},
                   {value: '4', label: ''},
                   {value: '5', label: ''}
                 ],
               },
               personInfo:{
                  itemId:'',

                  name:'',
                  idNumber:'',
                  mobile:'',
                  sex:'',
                  address:'',
                  email:'',
                  professionType:'',
                  salary:'',

                  itemTitle:'',
                  assetInfo:'',
                  borrowAmount:'',
                  borrowDeadline:'',
                  repayWay:'',
                  borrowRate:'',

                  idCardFirstUrl:'',
                  idCardSecondUrl:'',
                  bankCardFirstUrl:'',
                  bankCardSecondUrl:'',
               },
               sexList: [
                     {value: '', label: ''},
                     {value: '0', label: ''},
                     {value: '1', label: ''}
                ],
                professionTypeList: [
                     {value: '', label: ''},
                     {value: '1', label: ''},
                     {value: '2', label: ''},
                     {value: '3', label: ''},
                     {value: '4', label: ''},
                     {value: '5', label: ''},
                ],
                assetInfoList:[
                     {value: '', label: ''},
                     {value: '10', label: ''},
                     {value: '11', label: ''},
                     {value: '12', label: ''},
                     {value: '13', label: ''},
                     {value: '14', label: ''},
                     {value: '15', label: ''},
                     {value: '16', label: ''},
                ],
                borrowDeadlineList:[
                     {value: '', label: ''},
                     {value: '1', label: '0~3'},
                     {value: '2', label: '0~12'},
                     {value: '3', label: '0~24'},
                     {value: '4', label: '24'},
                ],
                repayWayList:[
                     {value: '', label: ''},
                     {value: '1', label: ''},
                     {value: '2', label: ''},
                     {value: '3', label: ''},
                ],
                rules:{
                  name: [{max: 20,required: true, message: '20', trigger: 'blur' }],
                  idNumber: [{validator:idcardRule,required: true,trigger: 'blur' }],
                  mobile: [{validator:mobileRule,required: true, trigger: 'blur' }],
                  sex: [{required: true, message: '', trigger: 'blur' }],
                  address: [{max: 100,required: true, message: '100', trigger: 'blur' }],
                  email: [{validator:emailRule,required: true, trigger: 'blur' }],
                  salary: [{validator:number_11_null,message: '', trigger: 'blur' }],

                  itemTitle: [{max: 20,required: true, message: '20', trigger: 'blur' }],
                  assetInfo: [{required: true, message: '', trigger: 'blur' }],
                  borrowAmount: [{validator:decimal_10_2,required: true,trigger: 'blur' }],
                  borrowDeadline: [{required: true, message: '', trigger: 'blur' }],
                  repayWay: [{required: true, message: '', trigger: 'blur' }],
                  borrowRate: [{validator:decimal_10_2,required: true,trigger: 'blur' }],

                  idCardFirstUrl: [{required: true, message: '', trigger: 'blur' }],
                  idCardSecondUrl: [{required: true, message: '', trigger: 'blur' }],
                  bankCardFirstUrl: [{required: true, message: '', trigger: 'blur' }],
                  bankCardSecondUrl: [{required: true, message: '', trigger: 'blur' }],
               },
               dialogAssetitemFormVisible: false,
               multipleSelection: [],
               dialogWidth:'700px',
               assetCompanyMenu:false,
               foundCompanyMenu:false,
               dialogAuditFormVisible: false,
               dialogWidth:'700px',
               dialogAuditWidth:'400px',
               audit:{
                  status:'',
                  id:'',
               },
               multipleSelection: []
      }
  },
  mounted(){
    var self=this;
    var userType = sessionStorage.userType;
    if(userType > 1){
      self.criteria.assetCompanyMenu = false;
      self.criteria.foundCompanyMenu = true;
    }else{//
      self.criteria.assetCompanyMenu = true;
      self.criteria.foundCompanyMenu = false;
    }
    self.reload();

  },
  methods: {
    //
    loadData: function (criteria, pageNum, pagesize) {
      var self = this;
      var params = {page: pageNum, size: pagesize, search: ""};
      if (criteria != null) {
        params.search=JSON.stringify({
          id:criteria.id,
          borrower_name:criteria.borrower_name,
          borrow_amount:criteria.borrow_amount,
          borrow_deadline:criteria.borrow_deadline,
          repay_way:criteria.repay_way,
          borrow_rate:criteria.borrow_rate,
          asset_info:criteria.asset_info,
          use_status:criteria.use_status
        });

      }
      getAssetItemPoolList(params).then(result => {
        var data = result.data;
        self.pagesize = data.pageSize;
        self.totalCount = data.total;
        self.tableData = data.list;
      });
    },
    reload: function () {
      var self = this;
      self.loadData(null, self.currentPage, self.pagesize);
    },
    restForm:function(){
        var self=this;
        if (self.$refs['personInfo']!==undefined) {
            self.$refs['personInfo'].resetFields();

            self.personInfo = {
                  itemId:'',

                  name:'',
                  idNumber:'',
                  mobile:'',
                  sex:'',
                  address:'',
                  email:'',
                  professionType:'',
                  salary:'',

                  itemTitle:'',
                  assetInfo:'',
                  borrowAmount:'',
                  borrowDeadline:'',
                  repayWay:'',
                  borrowRate:'',

                  idCardFirstUrl:'',
                  idCardSecondUrl:'',
                  bankCardFirstUrl:'',
                  bankCardSecondUrl:'',
               }
        }
        self.dialogAssetitemFormVisible = false;
     },
    close: function () {
      var self = this;
      self.dialogAuditFormVisible = false;
    },
    close1: function () {
      var self = this;
      self.restForm();
    },
    //
    handleSizeChange: function (val) {
      this.pagesize = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },
    //
    handleCurrentChange: function (val) {
      this.currentPage = val;
      this.loadData(this.criteria, this.currentPage, this.pagesize);
    },

    //
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    search: function () {
      var self = this;
      console.log("search");
      this.loadData(self.criteria, this.currentPage, this.pagesize);
    },
    handleAudit:function(index, row) {
      var self=this;
      self.audit.id = row.id;
      self.dialogAuditFormVisible = true;
    },
    toAudit:function(){
      var self=this;
      auditAssetItem({id:self.audit.id,status:self.audit.status}).then(result => {
        if(result.code!="200"){
          this.$message.error(result.message);
          return;
        }
        this.$message({type: 'success',message: '!'});
        self.dialogAuditFormVisible=false;
        self.reload();
      });
    },

    //
    handleAddCar: function(index, row) {
     var self=this;
      if(!confirm(""))return;
      addCar({id:row.id}).then(result => {
        alert(result.message);
        if(result.code!="200"){
          return;
        }
        self.reload();
      });
    },
    addPersonItem:function(){
      var self = this;
       self.firstCardItemName = 'baseInfo';
       self.dialogAssetitemFormVisible=true;
    }
    ,
    getFile(event,typeNum) {
         var file = event.target.files[0];
        if(file != undefined){
            this.uploadFile(file,typeNum,event);
            event.target.value = '';
         }
     },
     uploadFile(file,typeNum,event){
         event.preventDefault();
         var self=this;
         let formData = new FormData();
         formData.append("file",file);
         uploadFile({formData:formData,callback:function(result){
            console.log("uploadFile",result);
             if(result.code!="200"){
                this.$message.error(result.message);
                return;
             }
             if(typeNum == 1){
                self.personInfo.idCardFirstUrl=result.data.url;
             }
             else if(typeNum == 2){
                self.personInfo.idCardSecondUrl=result.data.url;
             }
             else if(typeNum == 3){
                self.personInfo.bankCardFirstUrl=result.data.url;
             }
             else if(typeNum == 4){
                self.personInfo.bankCardSecondUrl=result.data.url;
             }
        }});
      },
      toSavePerson: function () {
       var self = this;
       var formName = 'personInfo';
      self.$refs[formName].validate((valid) => {
        if(valid){
          var params = self.personInfo;

          addPersonItem(params).then(result => {
            var data = result.data;
            if(result.code!="200"){
                  this.$message.error(result.message);
                  return;
              }
            this.$message({type: 'success',message: '!'});
            self.reload();
            self.restForm();
          });
        }

      });

    },

    handleDetail: function(index, row){

    },
    export:function(criteria,exportType){//
      var self = this;
      var params = {search: ""};
      if (criteria != null && exportType == "1") {
        params.search=JSON.stringify({
          id:criteria.id,
          borrower_name:criteria.borrower_name,
          borrow_amount:criteria.borrow_amount,
          borrow_deadline:criteria.borrow_deadline,
          repay_way:criteria.repay_way,
          borrow_rate:criteria.borrow_rate,
          asset_info:criteria.asset_info,
          use_status:criteria.use_status
        });

      }
      var form = document.createElement('form');
      form.name = 'form'
      document.body.appendChild(form);
      for(var obj in params) {
        if(params.hasOwnProperty(obj)) {
          var input = document.createElement('input');
          input.tpye='hidden';
          input.name = obj;
          input.value = params[obj];
          form.appendChild(input)
        }
      }
      form.method = "post";//                                            ?access_token=
      //
      if(exportType == "1"){
        form.action = exportAssetItem();
      }
      //
      if(exportType == "2"){
        form.action = exportAssetItemCar();
      }

      form.submit();
      document.body.removeChild(form);
    },
    handleExport: function () {
      var self = this;
      this.export(self.criteria,"1");
    },
    handleExportCar: function () {
      var self = this;
      this.export(self.criteria,"2");
    },
    handleEdit: function(index, row){
       var self = this;
        self.personInfo.itemId = row.id;

       itemDetail({id:row.id}).then(result => {
          var data = result.data;

          self.personInfo.name = data.name;
          self.personInfo.idNumber = data.id_number;
          self.personInfo.mobile = data.mobile;
          self.personInfo.sex = data.sex + '';
          self.personInfo.address = data.address;
          self.personInfo.email = data.email;
          self.personInfo.professionType = data.profession_type + '';
          self.personInfo.salary = data.salary;

          self.personInfo.itemTitle = data.item_title;
          self.personInfo.assetInfo = data.asset_info  + '';
          self.personInfo.borrowAmount = data.borrow_amount;
          self.personInfo.borrowDeadline = data.borrow_deadline  + '';
          self.personInfo.repayWay = data.repay_way  + '';
          self.personInfo.borrowRate = data.borrow_rate;

          self.personInfo.idCardFirstUrl = data.id_card_first_url;
          self.personInfo.idCardSecondUrl = data.id_card_second_url;
          self.personInfo.bankCardFirstUrl = data.bank_card_first_url;
          self.personInfo.bankCardSecondUrl = data.bank_card_second_url;

          self.addPersonItem();

      });


    },
  }
}
</script>



